'use client'

import { useState } from 'react'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Input } from '@/components/ui/input'
import { Progress } from '@/components/ui/progress'

interface ReadingProps {
  selectedLevel: string
  language: string
}

interface ReadingText {
  id: number
  title: string
  summary: string
  content: string
  difficulty: 'easy' | 'medium' | 'hard' | 'very hard'
  readingTime: number
  category: string
  status: 'completed' | 'inProgress' | 'notStarted'
  questions?: Question[]
}

interface Question {
  id: number
  question: string
  options: string[]
  correctAnswer: number
}

// Aray√ºz tamamen Almanca
const translations = {
  title: 'Lesen - Deutsche Texte',
  subtitle: 'Verbessern Sie Ihr Leseverst√§ndnis mit authentischen deutschen Texten',
  search: 'Texte suchen...',
  categories: 'Kategorien',
  texts: 'Texte',
  completed: 'Gelesen',
  inProgress: 'In Bearbeitung',
  notStarted: 'Nicht begonnen',
  readText: 'Text lesen',
  continueReading: 'Weiterlesen',
  reviewText: 'Text wiederholen',
  difficulty: 'Schwierigkeit',
  readingTime: 'Lesezeit',
  minutes: 'Minuten',
  category: 'Kategorie',
  close: 'Schlie√üen',
  questions: 'Fragen',
  showQuestions: 'Fragen anzeigen',
  hideQuestions: 'Fragen ausblenden'
}

// Funktion zum Generieren von Beispieltexten
const generateTexts = (level: string, count: number): ReadingText[] => {
  const texts: ReadingText[] = []
  
  const categories = {
    A1: ['Familie', 'Alltag', 'Einkaufen', 'Hobbys', 'Wetter', 'Essen', 'Schule', 'Freizeit', 'Tiere', 'Farben'],
    A2: ['Reisen', 'Beruf', 'Kultur', 'Sport', 'Gesundheit', 'Wohnen', 'Verkehr', 'Medien', 'Natur', 'Feste'],
    B1: ['Umwelt', 'Bildung', 'Technologie', 'Politik', 'Wirtschaft', 'Gesellschaft', 'Kunst', 'Geschichte', 'Wissenschaft', 'Zukunft'],
    B2: ['Globalisierung', 'Innovation', 'Nachhaltigkeit', 'Digitalisierung', 'Kommunikation', 'Philosophie', 'Psychologie', 'Soziologie', 'Ethik', 'Forschung'],
    C1: ['Gesellschaft', 'Wissenschaft', 'Philosophie', 'Literatur', 'Komplexe Themen', 'Abstrakte Konzepte', 'Kritische Analyse', 'Theorien', 'Diskurse', 'Reflexion']
  }

  const difficulties = {
    A1: ['easy', 'easy', 'easy', 'medium'],
    A2: ['easy', 'medium', 'medium', 'medium'],
    B1: ['medium', 'medium', 'hard', 'hard'],
    B2: ['medium', 'hard', 'hard', 'very hard'],
    C1: ['hard', 'hard', 'very hard', 'very hard']
  }

  const readingTimes = {
    A1: [2, 3, 4, 5],
    A2: [4, 5, 6, 7],
    B1: [6, 7, 8, 9],
    B2: [8, 9, 10, 12],
    C1: [10, 12, 15, 18]
  }

  const sampleTitles = {
    A1: [
      'Meine Familie', 'Ein Tag in der Schule', 'Im Supermarkt', 'Mein Hobby', 'Das Wetter heute',
      'Mein Haustier', 'Am Wochenende', 'In der K√ºche', 'Meine Freunde', 'Der Park',
      'Im Restaurant', 'Mein Zimmer', 'Die Jahreszeiten', 'Beim Arzt', 'Im Zoo',
      'Meine Kleidung', 'Der Geburtstag', 'In der Stadt', 'Das Auto', 'Die Schule'
    ],
    A2: [
      'Urlaub in den Alpen', 'Arbeiten in Deutschland', 'Deutsche Feste', 'Sport treiben', 'Gesund leben',
      'Eine neue Wohnung', 'Mit dem Zug fahren', 'Fernsehen schauen', 'Im Wald spazieren', 'Weihnachtsmarkt',
      'Ein Bewerbungsgespr√§ch', 'Beim Zahnarzt', 'Eine Party organisieren', 'Im Museum', 'Kochen lernen',
      'Ein neues Hobby', 'Umzug nach Berlin', 'Deutsch lernen', 'Im Fitnessstudio', 'Einkaufen im Internet'
    ],
    B1: [
      'Umweltschutz im Alltag', 'Digitalisierung in der Bildung', 'Erneuerbare Energien', 'Social Media', 'Klimawandel',
      'K√ºnstliche Intelligenz', 'Demokratie heute', 'Globale Wirtschaft', 'Moderne Kunst', 'Deutsche Geschichte',
      'Medizinische Forschung', 'Die Zukunft der Arbeit', 'Stadtentwicklung', 'Internationale Politik', 'Kultureller Austausch',
      'Technologischer Fortschritt', 'Bildungssystem', 'Nachhaltigkeit', 'Migration', 'Europ√§ische Union'
    ],
    B2: [
      'Die Zukunft der Mobilit√§t', 'Globalisierung und Kultur', 'Innovation in der Medizin', 'Digitale Transformation', 'Interkulturelle Kommunikation',
      'Philosophie des Geistes', 'Psychologie der Motivation', 'Soziologie der Familie', 'Ethik in der Wissenschaft', 'Moderne Forschungsmethoden',
      'Gesellschaftlicher Wandel', 'Technologie und Mensch', 'Nachhaltige Entwicklung', 'Politische Systeme', 'Wirtschaftstheorien',
      'Kulturelle Identit√§t', 'Bildung der Zukunft', 'Umwelt und Gesellschaft', 'Medien und Demokratie', 'Internationale Beziehungen'
    ],
    C1: [
      'K√ºnstliche Intelligenz und Gesellschaft', 'Postmoderne Philosophie', 'Komplexe Literaturanalyse', 'Gesellschaftskritik', 'Abstrakte Denkmodelle',
      'Wissenschaftstheorie', 'Diskursanalyse', 'Kulturelle Hegemonie', 'Epistemologie', 'Hermeneutik',
      'Dekonstruktivismus', 'Ph√§nomenologie', 'Existenzialismus', 'Strukturalismus', 'Postkolonialismus',
      'Kritische Theorie', 'Semiotik', 'Psychoanalyse', 'Sozialkonstruktivismus', 'Interdisziplinarit√§t'
    ]
  }

  const levelCategories = categories[level as keyof typeof categories] || categories.A1
  const levelDifficulties = difficulties[level as keyof typeof difficulties] || difficulties.A1
  const levelReadingTimes = readingTimes[level as keyof typeof readingTimes] || readingTimes.A1
  const levelTitles = sampleTitles[level as keyof typeof sampleTitles] || sampleTitles.A1

  for (let i = 0; i < count; i++) {
    const categoryIndex = i % levelCategories.length
    const difficultyIndex = i % levelDifficulties.length
    const timeIndex = i % levelReadingTimes.length
    const titleIndex = i % levelTitles.length
    
    const status = i < count * 0.3 ? 'completed' : i < count * 0.5 ? 'inProgress' : 'notStarted'
    
    texts.push({
      id: i + 1,
      title: `${levelTitles[titleIndex]} ${Math.floor(i / levelTitles.length) > 0 ? `(${Math.floor(i / levelTitles.length) + 1})` : ''}`,
      summary: `Ein interessanter Text √ºber ${levelCategories[categoryIndex]} f√ºr ${level}-Niveau`,
      content: `
        <h3>${levelTitles[titleIndex]}</h3>
        <p>Dies ist ein Beispieltext f√ºr das ${level}-Niveau zum Thema ${levelCategories[categoryIndex]}. Der Text ist speziell f√ºr Deutschlernende auf diesem Niveau konzipiert.</p>
        <p>In diesem Text lernen Sie wichtige Vokabeln und Strukturen, die f√ºr das ${level}-Niveau relevant sind. Die Inhalte sind authentisch und praxisnah gestaltet.</p>
        <p>Jeder Text enth√§lt verschiedene grammatische Strukturen und einen angemessenen Wortschatz f√ºr Ihr Sprachniveau. So k√∂nnen Sie Ihr Leseverst√§ndnis systematisch verbessern.</p>
        <p>Die Texte behandeln verschiedene Themen aus dem Alltag, der Kultur, der Gesellschaft und anderen wichtigen Bereichen. Dies hilft Ihnen, ein breites Spektrum an Vokabular zu entwickeln.</p>
        <p>Nutzen Sie diese Texte regelm√§√üig, um Ihre Deutschkenntnisse zu verbessern und sich auf Pr√ºfungen vorzubereiten.</p>
      `,
      difficulty: levelDifficulties[difficultyIndex] as 'easy' | 'medium' | 'hard' | 'very hard',
      readingTime: levelReadingTimes[timeIndex],
      category: levelCategories[categoryIndex],
      status: status as 'completed' | 'inProgress' | 'notStarted',
      questions: i % 3 === 0 ? [
        {
          id: 1,
          question: `Was ist das Hauptthema dieses Textes √ºber ${levelCategories[categoryIndex]}?`,
          options: [
            `Grundlagen von ${levelCategories[categoryIndex]}`,
            `Geschichte von ${levelCategories[categoryIndex]}`,
            `Zukunft von ${levelCategories[categoryIndex]}`,
            `Probleme mit ${levelCategories[categoryIndex]}`
          ],
          correctAnswer: 0
        },
        {
          id: 2,
          question: `F√ºr welches Sprachniveau ist dieser Text geeignet?`,
          options: ['A1', 'A2', 'B1', level],
          correctAnswer: 3
        }
      ] : undefined
    })
  }

  return texts
}

const readingTexts = {
  A1: generateTexts('A1', 120),
  A2: generateTexts('A2', 110),
  B1: generateTexts('B1', 105),
  B2: generateTexts('B2', 108),
  C1: generateTexts('C1', 115)
}

export default function Reading({ selectedLevel, language }: ReadingProps) {
  const t = translations
  const texts = readingTexts[selectedLevel as keyof typeof readingTexts] || []
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedText, setSelectedText] = useState<number | null>(null)
  const [showQuestions, setShowQuestions] = useState(false)

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed':
        return 'bg-green-100 text-green-800'
      case 'inProgress':
        return 'bg-blue-100 text-blue-800'
      case 'notStarted':
        return 'bg-gray-100 text-gray-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed':
        return t.completed
      case 'inProgress':
        return t.inProgress
      case 'notStarted':
        return t.notStarted
      default:
        return t.notStarted
    }
  }

  const getButtonText = (status: string) => {
    switch (status) {
      case 'completed':
        return t.reviewText
      case 'inProgress':
        return t.continueReading
      case 'notStarted':
        return t.readText
      default:
        return t.readText
    }
  }

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy':
        return 'bg-green-100 text-green-800'
      case 'medium':
        return 'bg-yellow-100 text-yellow-800'
      case 'hard':
        return 'bg-orange-100 text-orange-800'
      case 'very hard':
        return 'bg-red-100 text-red-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const filteredTexts = texts.filter(text =>
    text.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    text.category.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const completedTexts = texts.filter(t => t.status === 'completed').length
  const inProgressTexts = texts.filter(t => t.status === 'inProgress').length
  const totalReadingTime = texts.reduce((sum, text) => sum + text.readingTime, 0)

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-r from-purple-600 to-pink-600 text-white rounded-lg p-6">
        <h2 className="text-2xl font-bold mb-2">{t.title}</h2>
        <p className="opacity-90">{t.subtitle}</p>
        <div className="mt-4 flex items-center space-x-4">
          <div className="text-sm">
            <span className="font-semibold">{selectedLevel}</span> Niveau
          </div>
          <div className="text-sm">
            <span className="font-semibold">{texts.length}</span> {t.texts}
          </div>
          <div className="text-sm">
            <span className="font-semibold">{completedTexts}</span> {t.completed}
          </div>
          <div className="text-sm">
            <span className="font-semibold">{Math.round((completedTexts/texts.length) * 100)}%</span> abgeschlossen
          </div>
        </div>
      </div>

      {/* Search */}
      <Card>
        <CardContent className="pt-6">
          <div className="relative">
            <Input
              type="text"
              placeholder={t.search}
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10"
            />
            <div className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground">
              üîç
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Reading Texts */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredTexts.slice(0, 12).map((text) => (
          <Card key={text.id} className="hover:shadow-lg transition-shadow cursor-pointer">
            <CardHeader>
              <div className="flex justify-between items-start mb-2">
                <CardTitle className="text-lg">{text.title}</CardTitle>
                <Badge className={getStatusColor(text.status)}>
                  {getStatusText(text.status)}
                </Badge>
              </div>
              <p className="text-sm text-muted-foreground">{text.summary}</p>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div className="flex flex-wrap gap-2">
                  <Badge className={getDifficultyColor(text.difficulty)}>
                    {t.difficulty}: {text.difficulty}
                  </Badge>
                  <Badge variant="outline">
                    üìñ {text.category}
                  </Badge>
                  <Badge variant="outline">
                    ‚è±Ô∏è {text.readingTime} {t.minutes}
                  </Badge>
                </div>

                {text.status === 'inProgress' && (
                  <div>
                    <div className="flex justify-between items-center mb-2">
                      <span className="text-sm font-medium">Fortschritt</span>
                      <span className="text-sm text-muted-foreground">65%</span>
                    </div>
                    <Progress value={65} />
                  </div>
                )}

                <Button 
                  className="w-full"
                  variant={text.status === 'completed' ? 'outline' : 'default'}
                  onClick={() => setSelectedText(text.id)}
                >
                  {getButtonText(text.status)}
                </Button>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Show More Button */}
      {filteredTexts.length > 12 && (
        <div className="text-center">
          <Button variant="outline" size="lg">
            Weitere {filteredTexts.length - 12} Texte anzeigen
          </Button>
        </div>
      )}

      {/* Text Reader Modal */}
      {selectedText && (
        <Card className="mt-6">
          <CardHeader>
            <div className="flex justify-between items-center">
              <CardTitle>
                {texts.find(t => t.id === selectedText)?.title}
              </CardTitle>
              <div className="flex space-x-2">
                {texts.find(t => t.id === selectedText)?.questions && (
                  <Button 
                    variant="outline" 
                    size="sm"
                    onClick={() => setShowQuestions(!showQuestions)}
                  >
                    ‚ùì {showQuestions ? t.hideQuestions : t.showQuestions}
                  </Button>
                )}
                <Button 
                  variant="outline" 
                  size="sm"
                  onClick={() => setSelectedText(null)}
                >
                  {t.close}
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <div 
              className="prose prose-lg max-w-none"
              dangerouslySetInnerHTML={{ 
                __html: texts.find(t => t.id === selectedText)?.content || '' 
              }}
            />

            {/* Questions Section */}
            {showQuestions && texts.find(t => t.id === selectedText)?.questions && (
              <div className="mt-6 p-4 bg-yellow-50 rounded-lg border-l-4 border-yellow-400">
                <h4 className="font-semibold mb-4">{t.questions}:</h4>
                <div className="space-y-4">
                  {texts.find(t => t.id === selectedText)?.questions?.map((question, index) => (
                    <div key={question.id} className="p-3 bg-white rounded border">
                      <p className="font-medium mb-2">{index + 1}. {question.question}</p>
                      <div className="space-y-1">
                        {question.options.map((option, optionIndex) => (
                          <div key={optionIndex} className="flex items-center space-x-2">
                            <input 
                              type="radio" 
                              name={`question-${question.id}`} 
                              id={`q${question.id}-${optionIndex}`}
                              className="text-blue-600"
                            />
                            <label 
                              htmlFor={`q${question.id}-${optionIndex}`}
                              className="text-sm cursor-pointer"
                            >
                              {String.fromCharCode(65 + optionIndex)}) {option}
                            </label>
                          </div>
                        ))}
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      )}

      {/* Statistics */}
      <Card>
        <CardHeader>
          <CardTitle>Lese-Statistiken</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="text-center p-4 bg-green-50 rounded-lg">
              <div className="text-2xl font-bold text-green-600">{completedTexts}</div>
              <div className="text-sm text-green-600">{t.completed}</div>
            </div>
            <div className="text-center p-4 bg-blue-50 rounded-lg">
              <div className="text-2xl font-bold text-blue-600">{inProgressTexts}</div>
              <div className="text-sm text-blue-600">{t.inProgress}</div>
            </div>
            <div className="text-center p-4 bg-gray-50 rounded-lg">
              <div className="text-2xl font-bold text-gray-600">
                {texts.length - completedTexts - inProgressTexts}
              </div>
              <div className="text-sm text-gray-600">{t.notStarted}</div>
            </div>
            <div className="text-center p-4 bg-purple-50 rounded-lg">
              <div className="text-2xl font-bold text-purple-600">{totalReadingTime}</div>
              <div className="text-sm text-purple-600">Gesamt {t.minutes}</div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}
