'use client'

import { Button } from '@/components/ui/button'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'

interface LevelSelectorProps {
  selectedLevel: string
  setSelectedLevel: (level: string) => void
  language: string
}

// Aray√ºz tamamen Almanca
const translations = {
  title: 'W√§hlen Sie Ihr Niveau',
  levels: {
    A1: 'A1 - Anf√§nger',
    A2: 'A2 - Grundlagen',
    B1: 'B1 - Mittelstufe',
    B2: 'B2 - Fortgeschritten',
    C1: 'C1 - Experte'
  }
}

const levelColors = {
  A1: 'bg-green-100 text-green-800 border-green-200 hover:bg-green-200',
  A2: 'bg-blue-100 text-blue-800 border-blue-200 hover:bg-blue-200',
  B1: 'bg-yellow-100 text-yellow-800 border-yellow-200 hover:bg-yellow-200',
  B2: 'bg-orange-100 text-orange-800 border-orange-200 hover:bg-orange-200',
  C1: 'bg-purple-100 text-purple-800 border-purple-200 hover:bg-purple-200'
}

const selectedLevelColors = {
  A1: 'bg-green-600 text-white border-green-600 hover:bg-green-700',
  A2: 'bg-blue-600 text-white border-blue-600 hover:bg-blue-700',
  B1: 'bg-yellow-600 text-white border-yellow-600 hover:bg-yellow-700',
  B2: 'bg-orange-600 text-white border-orange-600 hover:bg-orange-700',
  C1: 'bg-purple-600 text-white border-purple-600 hover:bg-purple-700'
}

export default function LevelSelector({ selectedLevel, setSelectedLevel, language }: LevelSelectorProps) {
  const t = translations
  const levels = ['A1', 'A2', 'B1', 'B2', 'C1']

  return (
    <Card className="w-full">
      <CardHeader>
        <CardTitle className="text-xl font-semibold">{t.title}</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
          {levels.map((level) => (
            <Button
              key={level}
              variant="outline"
              className={`p-4 h-auto flex flex-col items-center justify-center transition-all duration-200 ${
                selectedLevel === level
                  ? selectedLevelColors[level as keyof typeof selectedLevelColors]
                  : levelColors[level as keyof typeof levelColors]
              }`}
              onClick={() => setSelectedLevel(level)}
            >
              <div className="text-lg font-bold mb-1">{level}</div>
              <div className="text-xs text-center">
                {t.levels[level as keyof typeof t.levels].split(' - ')[1]}
              </div>
            </Button>
          ))}
        </div>
      </CardContent>
    </Card>
  )
}
