export default function Flashcards({ selectedLevel, language }: FlashcardsProps) {
  const t = translations
  const sets = flashcardSets[selectedLevel as keyof typeof flashcardSets] || []
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedSet, setSelectedSet] = useState<number | null>(null)
  const [currentCardIndex, setCurrentCardIndex] = useState(0)
  const [showAnswer, setShowAnswer] = useState(false)
  const [studyMode, setStudyMode] = useState(false)
  const [showDetails, setShowDetails] = useState(false)
  const [studyStats, setStudyStats] = useState({
    cardsStudied: 0,
    correctAnswers: 0,
    startTime: new Date()
  })

  // Ses √ßalma fonksiyonu
  const playAudio = (word: string) => {
    try {
      const utterance = new SpeechSynthesisUtterance(word)
      utterance.lang = 'de-DE'
      utterance.rate = 0.8
      utterance.pitch = 1
      
      const voices = speechSynthesis.getVoices()
      const germanVoice = voices.find(voice => voice.lang.includes('de'))
      if (germanVoice) {
        utterance.voice = germanVoice
      }
      
      speechSynthesis.speak(utterance)
    } catch (error) {
      console.error('Ses √ßalƒ±namadƒ±:', error)
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'mastered': return 'bg-green-100 text-green-800'
      case 'review': return 'bg-blue-100 text-blue-800'
      case 'learning': return 'bg-yellow-100 text-yellow-800'
      case 'new': return 'bg-gray-100 text-gray-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'mastered': return t.mastered
      case 'review': return t.review
      case 'learning': return t.learning
      case 'new': return t.new
      default: return t.new
    }
  }

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy': return 'bg-green-100 text-green-800'
      case 'medium': return 'bg-yellow-100 text-yellow-800'
      case 'hard': return 'bg-orange-100 text-orange-800'
      case 'very hard': return 'bg-red-100 text-red-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getWordTypeColor = (wordType: string) => {
    switch (wordType) {
      case 'isim': return 'bg-blue-100 text-blue-800'
      case 'fiil': return 'bg-green-100 text-green-800'
      case 'sƒ±fat': return 'bg-purple-100 text-purple-800'
      case 'zamir': return 'bg-orange-100 text-orange-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const handleCardResponse = (difficulty: 'again' | 'hard' | 'good' | 'easy') => {
    const currentSet = sets.find(s => s.id === selectedSet)
    if (!currentSet) return

    setStudyStats(prev => ({
      ...prev,
      cardsStudied: prev.cardsStudied + 1,
      correctAnswers: prev.correctAnswers + (difficulty === 'easy' || difficulty === 'good' ? 1 : 0)
    }))

    // N√§chste Karte oder Ende der Sitzung
    if (currentCardIndex < currentSet.cards.length - 1) {
      setCurrentCardIndex(currentCardIndex + 1)
      setShowAnswer(false)
      setShowDetails(false)
    } else {
      setStudyMode(false)
      // Hier k√∂nnte man die Statistiken speichern
    }
  }

  const startStudySession = (setId: number) => {
    setSelectedSet(setId)
    setCurrentCardIndex(0)
    setShowAnswer(false)
    setShowDetails(false)
    setStudyMode(true)
    setStudyStats({
      cardsStudied: 0,
      correctAnswers: 0,
      startTime: new Date()
    })
  }

  const filteredSets = sets.filter(set =>
    set.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    set.category.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const currentSet = sets.find(s => s.id === selectedSet)
  const currentCard = currentSet?.cards[currentCardIndex]

  const totalCards = sets.reduce((sum, set) => sum + set.totalCards, 0)
  const totalMastered = sets.reduce((sum, set) => sum + set.masteredCards, 0)

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-r from-indigo-600 to-purple-600 text-white rounded-lg p-6">
        <h2 className="text-2xl font-bold mb-2">{t.title}</h2>
        <p className="opacity-90">{t.subtitle}</p>
        <div className="mt-4 flex items-center space-x-4">
          <div className="text-sm">
            <span className="font-semibold">{selectedLevel}</span> Niveau
          </div>
          <div className="text-sm">
            <span className="font-semibold">{totalCards}</span> {t.cards}
          </div>
          <div className="text-sm">
            <span className="font-semibold">{totalMastered}</span> {t.mastered}
          </div>
          <div className="text-sm">
            <span className="font-semibold">{Math.round((totalMastered/totalCards) * 100)}%</span> abgeschlossen
          </div>
        </div>
      </div>

      {/* Search */}
      <Card>
        <CardContent className="pt-6">
          <div className="relative">
            <Input
              type="text"
              placeholder={t.search}
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10"
            />
            <div className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground">
              üîç
            </div>
          </div>
        </CardContent>
      </Card>

      {!studyMode ? (
        <>
          {/* Flashcard Sets */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredSets.map((set) => (
              <Card key={set.id} className="hover:shadow-lg transition-shadow cursor-pointer">
                <CardHeader>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center space-x-3">
                      <span className="text-2xl">{set.emoji}</span>
                      <div>
                        <CardTitle className="text-lg">{set.name}</CardTitle>
                        <p className="text-sm text-muted-foreground">
                          {set.masteredCards}/{set.totalCards} {t.cards}
                        </p>
                      </div>
                    </div>
                    <Badge className={set.color}>
                      {Math.round((set.masteredCards/set.totalCards) * 100)}%
                    </Badge>
                  </div>
                  <p className="text-sm text-muted-foreground mt-2">{set.description}</p>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <Progress value={(set.masteredCards/set.totalCards) * 100} />
                    
                    <div className="flex justify-between items-center text-sm text-muted-foreground">
                      <span>üìö {set.category}</span>
                      <span>{set.totalCards - set.masteredCards} zu lernen</span>
                    </div>

                    <Button 
                      className="w-full"
                      onClick={() => startStudySession(set.id)}
                    >
                      {set.masteredCards === set.totalCards ? t.reviewCards : t.startStudy}
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>

          {/* Statistics */}
          <Card>
            <CardHeader>
              <CardTitle>Lernkarten-Statistiken</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <div className="text-center p-4 bg-green-50 rounded-lg">
                  <div className="text-2xl font-bold text-green-600">{totalMastered}</div>
                  <div className="text-sm text-green-600">{t.mastered}</div>
                </div>
                <div className="text-center p-4 bg-yellow-50 rounded-lg">
                  <div className="text-2xl font-bold text-yellow-600">
                    {sets.reduce((sum, set) => sum + set.cards.filter(c => c.status === 'learning').length, 0)}
                  </div>
                  <div className="text-sm text-yellow-600">{t.learning}</div>
                </div>
                <div className="text-center p-4 bg-blue-50 rounded-lg">
                  <div className="text-2xl font-bold text-blue-600">
                    {sets.reduce((sum, set) => sum + set.cards.filter(c => c.status === 'review').length, 0)}
                  </div>
                  <div className="text-sm text-blue-600">{t.review}</div>
                </div>
                <div className="text-center p-4 bg-gray-50 rounded-lg">
                  <div className="text-2xl font-bold text-gray-600">
                    {sets.reduce((sum, set) => sum + set.cards.filter(c => c.status === 'new').length, 0)}
                  </div>
                  <div className="text-sm text-gray-600">{t.new}</div>
                </div>
              </div>
            </CardContent>
          </Card>
        </>
      ) : (
        /* Study Mode */
        <div className="max-w-2xl mx-auto">
          {/* Progress */}
          <Card className="mb-6">
            <CardContent className="pt-6">
              <div className="flex justify-between items-center mb-2">
                <span className="text-sm font-medium">
                  Karte {currentCardIndex + 1} von {currentSet?.cards.length}
                </span>
                <Button 
                  variant="outline" 
                  size="sm"
                  onClick={() => setStudyMode(false)}
                >
                  {t.close}
                </Button>
              </div>
              <Progress 
                value={((currentCardIndex + 1) / (currentSet?.cards.length || 1)) * 100} 
              />
            </CardContent>
          </Card>

          {/* Flashcard */}
          {currentCard && (
            <Card className="min-h-[400px] cursor-pointer" onClick={() => setShowAnswer(!showAnswer)}>
              <CardContent className="flex flex-col items-center justify-center h-full p-8">
                <div className="text-center space-y-4">
                  <div className="text-sm text-muted-foreground mb-4 flex flex-wrap gap-2 justify-center">
                    <Badge className={getStatusColor(currentCard.status)}>
                      {getStatusText(currentCard.status)}
                    </Badge>
                    <Badge className={getDifficultyColor(currentCard.difficulty)} variant="outline">
                      {currentCard.difficulty}
                    </Badge>
                    {currentCard.wordType && (
                      <Badge className={getWordTypeColor(currentCard.wordType)} variant="outline">
                        {currentCard.wordType}
                      </Badge>
                    )}
                  </div>

                  {!showAnswer ? (
                    /* Front of Card */
                    <div className="space-y-4">
                      <div className="text-3xl font-bold text-center">
                        {currentCard.front}
                      </div>
                      {currentCard.pronunciation && (
                        <div className="text-lg text-muted-foreground">
                          {currentCard.pronunciation}
                          <Button 
                            variant="ghost" 
                            size="sm" 
                            className="ml-2"
                            onClick={(e) => {
                              e.stopPropagation()
                              playAudio(currentCard.front)
                            }}
                          >
                            üîä
                          </Button>
                        </div>
                      )}
                      <div className="text-sm text-muted-foreground">
                        Klicken Sie, um die Antwort zu sehen
                      </div>
                    </div>
                  ) : (
                    /* Back of Card */
                    <div className="space-y-4">
                      <div className="text-2xl font-bold text-blue-600">
                        {currentCard.back}
                      </div>
                      {currentCard.example && (
                        <div className="text-lg text-muted-foreground italic">
                          "{currentCard.example}"
                        </div>
                      )}
                      
                      {/* Detaylƒ± a√ßƒ±klama g√∂sterme */}
                      {showDetails && currentCard.detailedExplanation && (
                        <div className="mt-3 p-3 bg-green-50 rounded-lg">
                          <p className="text-sm font-medium text-green-800">
                            {t.detailedExplanation}: {currentCard.detailedExplanation}
                          </p>
                        </div>
                      )}

                      <div className="flex gap-2 justify-center">
                        {currentCard.detailedExplanation && (
                          <Button 
                            size="sm" 
                            variant="outline"
                            onClick={(e) => {
                              e.stopPropagation()
                              setShowDetails(!showDetails)
                            }}
                          >
                            üìñ {t.showDetails}
                          </Button>
                        )}
                      </div>

                      <div className="text-sm text-muted-foreground">
                        Wie gut kannten Sie diese Karte?
                      </div>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          )}

          {/* Response Buttons */}
          {showAnswer && (
            <Card className="mt-4">
              <CardContent className="pt-6">
                <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                  <Button 
                    variant="outline"
                    className="text-red-600 border-red-200 hover:bg-red-50"
                    onClick={() => handleCardResponse('again')}
                  >
                    {t.again}
                  </Button>
                  <Button 
                    variant="outline"
                    className="text-orange-600 border-orange-200 hover:bg-orange-50"
                    onClick={() => handleCardResponse('hard')}
                  >
                    {t.hard}
                  </Button>
                  <Button 
                    variant="outline"
                    className="text-blue-600 border-blue-200 hover:bg-blue-50"
                    onClick={() => handleCardResponse('good')}
                  >
                    {t.good}
                  </Button>
                  <Button 
                    variant="outline"
                    className="text-green-600 border-green-200 hover:bg-green-50"
                    onClick={() => handleCardResponse('easy')}
                  >
                    {t.easy}
                  </Button>
                </div>
              </CardContent>
            </Card>
          )}

          {/* Study Stats */}
          <Card className="mt-4">
            <CardContent className="pt-6">
              <div className="grid grid-cols-3 gap-4 text-center">
                <div>
                  <div className="text-2xl font-bold text-blue-600">{studyStats.cardsStudied}</div>
                  <div className="text-sm text-muted-foreground">{t.cardsStudied}</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-green-600">
                    {studyStats.cardsStudied > 0 ? Math.round((studyStats.correctAnswers / studyStats.cardsStudied) * 100) : 0}%
                  </div>
                  <div className="text-sm text-muted-foreground">{t.accuracy}</div>
                </div>
                <div>
                  <div className="text-2xl font-bold text-purple-600">
                    {Math.round((Date.now() - studyStats.startTime.getTime()) / 60000)}
                  </div>
                  <div className="text-sm text-muted-foreground">{t.minutes}</div>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  )
}
