'use client'

import { useState } from 'react'
import Header from '@/components/Header'
import Navigation from '@/components/Navigation'
import LevelSelector from '@/components/LevelSelector'
import Dashboard from '@/components/pages/Dashboard'
import Grammar from '@/components/pages/GrammarNew'
import Vocabulary from '@/components/pages/VocabularyNew'
import Reading from '@/components/pages/ReadingNew'
import Exercises from '@/components/pages/ExercisesNew'
import Progress from '@/components/pages/Progress'
import Profile from '@/components/pages/Profile'
import Flashcards from '@/components/pages/FlashcardsNew'

export default function Home() {
  const [activeSection, setActiveSection] = useState('dashboard')
  const [selectedLevel, setSelectedLevel] = useState('B1')
  const [language, setLanguage] = useState('tr')

  const renderActiveSection = () => {
    switch (activeSection) {
      case 'dashboard':
        return <Dashboard selectedLevel={selectedLevel} language={language} />
      case 'grammar':
        return <Grammar selectedLevel={selectedLevel} language={language} />
      case 'vocabulary':
        return <Vocabulary selectedLevel={selectedLevel} language={language} />
      case 'reading':
        return <Reading selectedLevel={selectedLevel} language={language} />
      case 'exercises':
        return <Exercises selectedLevel={selectedLevel} language={language} />
      case 'progress':
        return <Progress selectedLevel={selectedLevel} language={language} />
      case 'profile':
        return <Profile language={language} />
      case 'flashcards':
        return <Flashcards selectedLevel={selectedLevel} language={language} />
      default:
        return <Dashboard selectedLevel={selectedLevel} language={language} />
    }
  }

  return (
    <div className="min-h-screen bg-background">
      <Header language={language} setLanguage={setLanguage} />
      <Navigation 
        activeSection={activeSection} 
        setActiveSection={setActiveSection}
        language={language}
      />
      
      <main className="container mx-auto p-4 md:p-6">
        <LevelSelector 
          selectedLevel={selectedLevel}
          setSelectedLevel={setSelectedLevel}
          language={language}
        />
        
        <div className="mt-6">
          {renderActiveSection()}
        </div>
      </main>
    </div>
  )
}
