export default function Exercises({ selectedLevel, language }: ExercisesProps) {
  const t = translations
  const exerciseList = exercises[selectedLevel as keyof typeof exercises] || []
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedExercise, setSelectedExercise] = useState<number | null>(null)
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [userAnswers, setUserAnswers] = useState<{[key: number]: string | number}>({})
  const [showResults, setShowResults] = useState(false)
  const [exerciseCompleted, setExerciseCompleted] = useState(false)

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'completed':
        return 'bg-green-100 text-green-800'
      case 'inProgress':
        return 'bg-blue-100 text-blue-800'
      case 'notStarted':
        return 'bg-gray-100 text-gray-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusText = (status: string) => {
    switch (status) {
      case 'completed':
        return t.completed
      case 'inProgress':
        return t.inProgress
      case 'notStarted':
        return t.notStarted
      default:
        return t.notStarted
    }
  }

  const getButtonText = (status: string) => {
    switch (status) {
      case 'completed':
        return t.reviewExercise
      case 'inProgress':
        return t.continueExercise
      case 'notStarted':
        return t.startExercise
      default:
        return t.startExercise
    }
  }

  const getDifficultyColor = (difficulty: string) => {
    switch (difficulty) {
      case 'easy':
        return 'bg-green-100 text-green-800'
      case 'medium':
        return 'bg-yellow-100 text-yellow-800'
      case 'hard':
        return 'bg-orange-100 text-orange-800'
      case 'very hard':
        return 'bg-red-100 text-red-800'
      default:
        return 'bg-gray-100 text-gray-800'
    }
  }

  const handleAnswerChange = (questionId: number, answer: string | number) => {
    setUserAnswers(prev => ({
      ...prev,
      [questionId]: answer
    }))
  }

  const calculateScore = () => {
    const exercise = exerciseList.find(ex => ex.id === selectedExercise)
    if (!exercise) return 0

    let correct = 0
    exercise.questions.forEach(question => {
      const userAnswer = userAnswers[question.id]
      if (userAnswer === question.correctAnswer) {
        correct++
      }
    })

    return Math.round((correct / exercise.questions.length) * exercise.points)
  }

  const filteredExercises = exerciseList.filter(exercise =>
    exercise.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
    exercise.category.toLowerCase().includes(searchTerm.toLowerCase())
  )

  const currentExercise = exerciseList.find(ex => ex.id === selectedExercise)
  const currentQuestionData = currentExercise?.questions[currentQuestion]

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-gradient-to-r from-green-600 to-teal-600 text-white rounded-lg p-6">
        <h2 className="text-2xl font-bold mb-2">{t.title}</h2>
        <p className="opacity-90">{t.subtitle}</p>
        <div className="mt-4 flex items-center space-x-4">
          <div className="text-sm">
            <span className="font-semibold">{selectedLevel}</span> Niveau
          </div>
          <div className="text-sm">
            <span className="font-semibold">{exerciseList.length}</span> {t.exercises}
          </div>
          <div className="text-sm">
            <span className="font-semibold">{exerciseList.filter(ex => ex.status === 'completed').length}</span> {t.completed}
          </div>
        </div>
      </div>

      {/* Search */}
      <Card>
        <CardContent className="pt-6">
          <div className="relative">
            <Input
              type="text"
              placeholder={t.search}
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10"
            />
            <div className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground">
              üîç
            </div>
          </div>
        </CardContent>
      </Card>

      {!selectedExercise ? (
        <>
          {/* Exercise List */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredExercises.map((exercise) => (
              <Card key={exercise.id} className="hover:shadow-lg transition-shadow cursor-pointer">
                <CardHeader>
                  <div className="flex justify-between items-start mb-2">
                    <CardTitle className="text-lg">{exercise.title}</CardTitle>
                    <Badge className={getStatusColor(exercise.status)}>
                      {getStatusText(exercise.status)}
                    </Badge>
                  </div>
                  <p className="text-sm text-muted-foreground">{exercise.description}</p>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div className="flex flex-wrap gap-2">
                      <Badge className={getDifficultyColor(exercise.difficulty)}>
                        {t.difficulty}: {exercise.difficulty}
                      </Badge>
                      <Badge variant="outline">
                        üìö {exercise.category}
                      </Badge>
                      <Badge variant="outline">
                        ‚≠ê {exercise.points} {t.points}
                      </Badge>
                      {exercise.timeLimit && (
                        <Badge variant="outline">
                          ‚è±Ô∏è {exercise.timeLimit} {t.minutes}
                        </Badge>
                      )}
                    </div>

                    <div className="text-sm text-muted-foreground">
                      {exercise.questions.length} Fragen
                    </div>

                    {exercise.status === 'inProgress' && (
                      <div>
                        <div className="flex justify-between items-center mb-2">
                          <span className="text-sm font-medium">Fortschritt</span>
                          <span className="text-sm text-muted-foreground">60%</span>
                        </div>
                        <Progress value={60} />
                      </div>
                    )}

                    <Button 
                      className="w-full"
                      variant={exercise.status === 'completed' ? 'outline' : 'default'}
                      onClick={() => {
                        setSelectedExercise(exercise.id)
                        setCurrentQuestion(0)
                        setUserAnswers({})
                        setShowResults(false)
                        setExerciseCompleted(false)
                      }}
                    >
                      {getButtonText(exercise.status)}
                    </Button>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>

          {/* Statistics */}
          <Card>
            <CardHeader>
              <CardTitle>√úbungs-Statistiken</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
                <div className="text-center p-4 bg-green-50 rounded-lg">
                  <div className="text-2xl font-bold text-green-600">
                    {exerciseList.filter(ex => ex.status === 'completed').length}
                  </div>
                  <div className="text-sm text-green-600">{t.completed}</div>
                </div>
                <div className="text-center p-4 bg-blue-50 rounded-lg">
                  <div className="text-2xl font-bold text-blue-600">
                    {exerciseList.filter(ex => ex.status === 'inProgress').length}
                  </div>
                  <div className="text-sm text-blue-600">{t.inProgress}</div>
                </div>
                <div className="text-center p-4 bg-gray-50 rounded-lg">
                  <div className="text-2xl font-bold text-gray-600">
                    {exerciseList.filter(ex => ex.status === 'notStarted').length}
                  </div>
                  <div className="text-sm text-gray-600">{t.notStarted}</div>
                </div>
                <div className="text-center p-4 bg-purple-50 rounded-lg">
                  <div className="text-2xl font-bold text-purple-600">
                    {exerciseList.reduce((sum, ex) => sum + ex.points, 0)}
                  </div>
                  <div className="text-sm text-purple-600">Gesamt {t.points}</div>
                </div>
              </div>
            </CardContent>
          </Card>
        </>
      ) : (
        /* Exercise Interface */
        <Card>
          <CardHeader>
            <div className="flex justify-between items-center">
              <div>
                <CardTitle>{currentExercise?.title}</CardTitle>
                <p className="text-sm text-muted-foreground mt-1">
                  Frage {currentQuestion + 1} von {currentExercise?.questions.length}
                </p>
              </div>
              <Button 
                variant="outline" 
                size="sm"
                onClick={() => setSelectedExercise(null)}
              >
                {t.close}
              </Button>
            </div>
            <Progress 
              value={((currentQuestion + 1) / (currentExercise?.questions.length || 1)) * 100} 
              className="mt-4"
            />
          </CardHeader>
          <CardContent>
            {!showResults && currentQuestionData ? (
              <div className="space-y-6">
                <div className="text-lg font-medium">
                  {currentQuestionData.question}
                </div>

                {/* Multiple Choice */}
                {currentExercise?.type === 'multiple-choice' && currentQuestionData.options && (
                  <RadioGroup
                    value={userAnswers[currentQuestionData.id]?.toString() || ''}
                    onValueChange={(value) => handleAnswerChange(currentQuestionData.id, parseInt(value))}
                  >
                    {currentQuestionData.options.map((option, index) => (
                      <div key={index} className="flex items-center space-x-2">
                        <RadioGroupItem value={index.toString()} id={`option-${index}`} />
                        <Label htmlFor={`option-${index}`} className="cursor-pointer">
                          {option}
                        </Label>
                      </div>
                    ))}
                  </RadioGroup>
                )}

                {/* Fill in the Blank */}
                {currentExercise?.type === 'fill-blank' && (
                  <Input
                    placeholder="Ihre Antwort..."
                    value={userAnswers[currentQuestionData.id]?.toString() || ''}
                    onChange={(e) => handleAnswerChange(currentQuestionData.id, e.target.value)}
                    className="text-lg"
                  />
                )}

                {/* Navigation Buttons */}
                <div className="flex justify-between items-center">
                  <Button
                    variant="outline"
                    onClick={() => setCurrentQuestion(Math.max(0, currentQuestion - 1))}
                    disabled={currentQuestion === 0}
                  >
                    {t.previousQuestion}
                  </Button>

                  {currentQuestion < (currentExercise?.questions.length || 1) - 1 ? (
                    <Button
                      onClick={() => setCurrentQuestion(currentQuestion + 1)}
                      disabled={!userAnswers[currentQuestionData.id]}
                    >
                      {t.nextQuestion}
                    </Button>
                  ) : (
                    <Button
                      onClick={() => setShowResults(true)}
                      disabled={!userAnswers[currentQuestionData.id]}
                    >
                      {t.showResult}
                    </Button>
                  )}
                </div>
              </div>
            ) : (
              /* Results */
              <div className="space-y-6">
                <div className="text-center">
                  <h3 className="text-2xl font-bold mb-2">Ergebnis</h3>
                  <div className="text-4xl font-bold text-blue-600 mb-2">
                    {calculateScore()}/{currentExercise?.points} {t.points}
                  </div>
                  <div className="text-lg text-muted-foreground">
                    {Math.round((calculateScore() / (currentExercise?.points || 1)) * 100)}% richtig
                  </div>
                </div>

                {/* Answer Review */}
                <div className="space-y-4">
                  <h4 className="font-semibold">Antworten im Detail:</h4>
                  {currentExercise?.questions.map((question, index) => {
                    const userAnswer = userAnswers[question.id]
                    const isCorrect = userAnswer === question.correctAnswer
                    
                    return (
                      <div key={question.id} className={`p-4 rounded-lg border-l-4 ${
                        isCorrect ? 'bg-green-50 border-green-400' : 'bg-red-50 border-red-400'
                      }`}>
                        <div className="flex items-start justify-between mb-2">
                          <span className="font-medium">Frage {index + 1}: {question.question}</span>
                          <Badge className={isCorrect ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}>
                            {isCorrect ? t.correct : t.incorrect}
                          </Badge>
                        </div>
                        
                        {currentExercise.type === 'multiple-choice' && question.options && (
                          <div className="text-sm">
                            <p>Ihre Antwort: {question.options[userAnswer as number] || 'Keine Antwort'}</p>
                            <p>Richtige Antwort: {question.options[question.correctAnswer as number]}</p>
                          </div>
                        )}
                        
                        {currentExercise.type === 'fill-blank' && (
                          <div className="text-sm">
                            <p>Ihre Antwort: {userAnswer || 'Keine Antwort'}</p>
                            <p>Richtige Antwort: {question.correctAnswer}</p>
                          </div>
                        )}
                        
                        {question.explanation && (
                          <div className="mt-2 text-sm text-blue-600">
                            <strong>{t.explanation}:</strong> {question.explanation}
                          </div>
                        )}
                      </div>
                    )
                  })}
                </div>

                <div className="flex justify-center space-x-4">
                  <Button
                    variant="outline"
                    onClick={() => {
                      setCurrentQuestion(0)
                      setUserAnswers({})
                      setShowResults(false)
                    }}
                  >
                    Nochmal √ºben
                  </Button>
                  <Button onClick={() => setSelectedExercise(null)}>
                    Zur√ºck zur √úbersicht
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
      )}
    </div>
  )
}
